import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/eventCenter/api";
import { EventRecordEntity } from "./interface";
import cookieUtils from "@/utils/cookie";

@Component({})
export default class EventRecordDetail extends Vue {
  activeTabName: any = "first";
  form: EventRecordEntity | null = null;
  currentNodeImg: string = "";

  created() {
    let item: any = this.$route.params.item;
    if (item) {
      this.form = item;
      this.currentNodeImg = `${api.getProcessImg}?processInstanceId=${item.processInstanceId}`;
    } else {
      this.$router.back();
    }
  }

  test(data: any) {
    if (data.name === "second") {
      this.getImage();
    }
  }

  mounted() {
    // this.getImage();
  }

  getImage() {
    this.$nextTick(() => {
      let img: any = this.$refs["processImg"];
      let url = img.getAttribute("process-src");
      let request = new XMLHttpRequest();
      let accessToken = cookieUtils.get("accessToken");
      request.responseType = "blob";
      request.open("get", url, true);
      request.setRequestHeader("accessToken", "Bearer " + accessToken);
      request.onreadystatechange = e => {
        if (request.readyState == XMLHttpRequest.DONE && request.status == 200) {
          img.src = URL.createObjectURL(request.response);
          img.onload = () => {
            URL.revokeObjectURL(img.src);
          };
        } else {
          // this.$message("流程节点查询失败");
        }
        // this.imgLoading = false;
      };
      request.send(null);
    });
  }
}
